import { MultiSelect } from "react-multi-select-component"

const localization = {
    allItemsAreSelected: " ",
    clearSearch: " ",
    clearSelected: "  ",
    noOptions: " ",
    search: "",
    selectAll: " ",
    selectAllFiltered: "  ()",
    create: ""
}

function MultiFieldSelect(props) {

    const options = []

    props.headers.forEach((option) => {
        if (props.intersection.includes(option)) {
            options.push({label: option, value: option, disabled: true})
        } else {
            options.push({label: option, value: option})
        }
    })

    if (options.length === 0) {
        localization["selectSomeItems"] = " 2 "
    } else {
        localization["selectSomeItems"] = " ..."
    }

    return (
        <div id="multiSelect">
            <label className="form-label"> 2  </label>
            <MultiSelect 
                options={options}
                value={props.appendList}
                disableSearch={true}
                overrideStrings={localization}
                onChange={props.setAppendList}
                disabled={options.length === 0}
            />
        </div>
    );
    
}

export default MultiFieldSelect