function CommonFieldSelect(props) {

    const options = []
    props.options.forEach((option, index) =>  {
        options.push(<option key={index} value={option}>{option}</option>)
    });

    const defaultOption = options.length === 0 ? "  " : " "

    const onSelect = (event) => {
        props.setComparator(event.target.value)
    }

    return (
        <div id="commonSelect">
            <label className="form-label"> 1  </label>
            <select defaultValue="default" onChange={onSelect} className="form-select" disabled={options.length === 0}>
                <option value="default" disabled hidden>{defaultOption}</option>
                {options}
            </select>
        </div>
    );
    
}

export default CommonFieldSelect